import React, { useEffect, useState } from 'react';
import './styles.scss';

const Test01 = () => {
  const [imageWidth, setImageWidth] = useState(0);
  useEffect(() => {
    console.log('load image');
    $.reel.scan();

    window.addEventListener('scroll', (e) => {
      console.log('====> yyyyy');
      //   const scroll = document.getElementById('test-01').scrollTop();
    });
  }, []);

  const scrollImage = (e) => {
    const top = e.target.scrollTop;
    console.log('scrollImage', top);
    setImageWidth(top);
    $('#image-test').css({
      width: 500 + top + 'px',
    });
  };

  return (
    <div className='test-01' onScroll={scrollImage}>
      <img
        src='/image/00000.jpg'
        width={500 + imageWidth}
        height={500 + imageWidth}
        id='image-test'
        className='reel'
        data-images='/image/00###.jpg|000..144'
        data-frames='9'
        data-rows='16'
        data-row='1'
        data-responsive='true'
        // data-footage='24'
        // data-cw='true'
        // data-orbital='24'
        // data-inversed='true'
        // data-speed='0.2'
      ></img>
    </div>
  );
};

export default React.memo(Test01);
